import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import logo from '../assets/images/logo.svg';
import { Link } from 'react-router-dom';
import {LinkContainer} from 'react-router-bootstrap'
import { useState } from 'react';

export default function NavBar() {
  const [expanded, setExpanded] = useState(false);

  function handleLinkClick() {
    setExpanded(false);
  }

  return (
    <Navbar
      expanded={expanded}
      onToggle={setExpanded}
      collapseOnSelect
      expand="lg"
      className='nfBar'
      variant="light"
    >
      <Container>
        <Navbar.Brand>
          <LinkContainer to="/">
            <Nav.Link className='logoImgContainer'><img className='logoImg' src={logo} alt="Logo" /></Nav.Link>
            </LinkContainer>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" className='hamburger' />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="ml-auto">
          <LinkContainer to="/about">
              <Nav.Link className='navLinkChild' onClick={handleLinkClick}>About</Nav.Link>
            </LinkContainer>
            <LinkContainer to="/contact">
              <Nav.Link className='navLinkChild' onClick={handleLinkClick}>Contact</Nav.Link>
            </LinkContainer>
            <LinkContainer to="/services">
              <Nav.Link className='navLinkChild' onClick={handleLinkClick}>Services</Nav.Link>
            </LinkContainer>
            <LinkContainer to="/bookNow">
            <Nav.Link className='navLinkChild' id='booknowNav' onClick={(event) => {
              event.preventDefault();
              handleLinkClick();
              window.Calendly.initPopupWidget({ url: 'https://calendly.com/bcourt95' });
              }}>Book Now</Nav.Link>
            </LinkContainer>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}
